import React from "react";
import GitHubIcon from "@material-ui/icons/GitHub";
import LinkedInIcon from "@material-ui/icons/LinkedIn";
import CloudDownloadRounded from "@material-ui/icons/CloudDownloadRounded";
import "../styles/Home.css";

function Home() {
  return (
    <div className="home">
      <div className="about">
        <h2>Hi, My Name is Tri</h2>
        <div className="prompt">
          <p>
            A Software Engineer with a passion for Japanese culture and
            Technology
          </p>
          <a href="https://www.linkedin.com/in/ngonhattri">
            <LinkedInIcon />
          </a>
          <a href="https://drive.google.com/file/d/1q6qQijyJG0k9PVeCOulQTgSSu-J3oGAW/view?usp=sharing">
            <CloudDownloadRounded />
          </a>
          <a href="https://github.com/ngonhattri">
            <GitHubIcon />
          </a>
        </div>
      </div>
      <div className="skills">
        <h1>Skills</h1>
        <ol className="list">
          <li className="item">
            <h2>Front-End</h2>
            <span>ReatJS, HTML, CSS, Flutter, BootStrap, MaterialUI, Yarn</span>
          </li>
          <li className="item">
            <h2>Back-End</h2>
            <span>NodeJS, PHP, MySQL, ExpressJS, Firebase</span>
          </li>
          <li className="item">
            <h2>Mobile</h2>
            <span>Java(Android), Swift(iOS), Flutter</span>
          </li>
          <li className="item">
            <h2>Languages</h2>
            <span>Java, JavaScript, Python, PHP, TypeScript</span>
          </li>
        </ol>
      </div>
    </div>
  );
}

export default Home;
